import React from 'react';
import Breadcrumb from '../../components/common/Breadcrumb';


const withBreadcrumb = containerClassName => Comp => {
    const page = (props, context) => {
        return (
            <div className={containerClassName}>
                <Breadcrumb match={props.match} />
                <Comp {...props} />
            </div>
        );
    };

    Comp.getInitialProps = (ctx) => {
        console.log('aaaaa', ctx);
    }
    // page.getInitialProps = ctx => {
    //     return Comp.getInitialProps && Comp.getInitialProps(ctx);
    // };
    console.log('CHECK', Comp.getInitialProps);

    return page;
};


export default withBreadcrumb;